h1 {
  margin: 0;
}

header img {
  height: 4rem;
  width: 4rem;
}

header img.right {
  margin: 1rem 0 2.68rem;
  -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
          transform: scaleX(-1);
}

header img.left {
  margin: 2.68rem 0 1rem;
}

@media (min-width: 62em) {
  h1 {
    margin: 2.68rem 0 1rem;
  }

  header img.left,
  header img.right {
    margin: 0;
  }
}



